/** @jsxImportSource @emotion/react */
import { css } from "@emotion/react";
import * as React from "react";
import Button from "@mui/material/Button";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import Slide from "@mui/material/Slide";
import { TransitionProps } from "@mui/material/transitions";
import { motion } from "framer-motion";

type dialogProps = {
  img: any;
  text: string;
};

const Transition = React.forwardRef(function Transition(
  props: TransitionProps & {
    children: React.ReactElement<any, any>;
  },
  ref: React.Ref<unknown>
) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export default function AlertDialogSlide(props: dialogProps) {
  const dialogImageStyle = css`
    width: 500px;
    height: 500px;
    object-fit: cover;
    @media (max-width: 900px) {
      width: 250px;
      height: 250px;
    }
  `;

  const imageStyle = css`
    width: 250px;
    height: 250px;
    cursor: pointer;
    filter: grayscale(100%);
    border-radius: 10px;
    transition: transform 0.3s ease-in-out;
    object-fit: cover;
    &:hover {
      opacity: 0.7;
      transform: scale(1.1);
    }
    &:active {
      transform: scale(1.1);
    }
    @media (max-width: 900px) {
      width: 200px;
      height: 200px;
    }
  `;

  const [open, setOpen] = React.useState(false);
  const image = props.img;

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <React.Fragment>
      <img src={image} css={imageStyle} onClick={handleClickOpen} />
      <Dialog
        open={open}
        TransitionComponent={Transition}
        keepMounted
        onClose={handleClose}
        aria-describedby="alert-dialog-slide-description"
      >
        <DialogTitle
          sx={{
            fontSize: "0.8rem",
            backgroundColor: "gray",
            color: "white",
          }}
        >
          {props.text}
        </DialogTitle>
        <DialogContent sx={{ backgroundColor: "gray" }}>
          <DialogContentText
            sx={{ justifyContent: "center", display: "flex" }}
            id="alert-dialog-slide-description"
          >
            <img src={image} css={dialogImageStyle} alt="images" />
          </DialogContentText>
        </DialogContent>
      </Dialog>
    </React.Fragment>
  );
}
