/** @jsxImportSource @emotion/react */
import { css } from "@emotion/react";
import React, { ReactNode } from "react";
import { motion } from "framer-motion";

/* https://jfelix.info/blog/page-transitions-in-react */

const blackBox = {
  initial: {
    height: "100vh",
    bottom: 0,
  },
  animate: {
    height: 0,
    opacity: 1,
    transition: {
      duration: 1,
      ease: [0.87, 0, 0.13, 1],
    },
  },
};

const InitialTransition = ({ children }: { children: ReactNode }) => {
  const divStyle = css`
    align-items: center;
    justify-content: center;
    width: 100%;
    position: absolute;
  `;

  const motionStyle = css`
    position: relative;
    background-color: gray;
  `;

  return (
    <div css={divStyle}>
      <motion.div
        css={motionStyle}
        initial="initial"
        animate="animate"
        variants={blackBox}
        // onAnimationStart={() => document.body.classList.add("overflow-hidden")}
        // onAnimationComplete={() =>
        //   document.body.classList.remove("overflow-hidden")
        // }
      />
      {children}
    </div>
  );
};

export default InitialTransition;
